@import 'includes/master/mixins';

.blog-banner {
    height: 500px;

    .content {
        width: 100%;
        max-width: 100%;

        .text {
            margin-bottom: 0;

        }
    }
}

.article-list {
    padding: 60px 45px;
}

.article {
    padding: 20px;
    position: relative;

    .link-overlay {
        z-index: 11;

        &:hover {
            ~img {
                filter: brightness(120%);
            }

            ~.content {
                .title {
                    color: var(--chover);
                }
            }
        }
    }

    img {
        width: 100%;
        pointer-events: none;
        @include transition(all 0.2s);
    }

    .content {
        padding: 20px;
        pointer-events: none;

        .top {
            @include flexbox();
            justify-content: space-between;

            .sub-title {
                font-size: 12px;
                color: rgba(65, 74, 90, 0.5);
                line-height: 25px;
            }

            .time {
                color: var(--cmain);
                font-size: 12px;
                line-height: 16px;
            }


        }

        .title {
            font-size: 20px;
            line-height: 25px;
            color: var(--cblack);
        }

        .writer {
            font-size: 12px;
            line-height: 25px;
            color: var(--cmain);
        }

        .text {
            font-size: 12px;
            line-height: 16px;
            color: var(--cmain);
            opacity: 0.8;
        }
    }
}

.article-detail {
    color: var(--cblack);
    padding: 60px 65px;
    padding-top: 30px;

    img {
        width: calc(100% - 15px);
        margin: 30px 0;
    }

    .wrap-with50 {
        @include flexbox();
        align-items: center;
    }

    .with-50 {
        width: 50%;

        &:first-child {
            margin-right: 15px;
        }

        &:last-child {
            margin-left: 15px;
        }
    }

    .title {
        font-size: 36px;
        line-height: 65px;

    }

    .text {
        font-size: 18px;
        line-height: 36px;
    }
    .text-img{
        font-style: italic;
        font-size: 20px;
        margin-bottom: 10px;
    }

    .title,
    .text-img,
    .text {
        padding: 0 20%;
    }

    .btn-blog {
        font-weight: 500;
        font-size: 18px;
        line-height: 22px;
        text-transform: uppercase;
        @include flexbox();
        align-items: center;

        &.left svg {
            margin-right: 15px;
        }

        &.right svg {
            margin-left: 15px;
            margin-right: 15px;
        }

        &:hover {
            svg path {
                fill: var(--chover);
            }
        }
    }

    .btn-bottom {
        display: flex;
        align-items: center;
        justify-content: space-between;
        .left svg {
            margin-left: 15px;
        }
        svg{
            width: 100%;
            min-width: 48px;
        }
        .btn-blog {
            @include flexbox();
            align-items: center;
            white-space: nowrap;
            img{
                width: 80px;
            }
        }
    }
}
.btn-wrap-news-list{
    text-align: center;
    width: 100%;
    margin-bottom: 60px;
    a{
        display: inline-block;
        padding: 17px 50px;
        background-color: var(--chover);
        color: var(--cwhite);
        &:hover{
            background-color: rgb(165, 159, 139);
        }
    }
}