@import 'mixins';
header {
    z-index: 111;
    padding: 0 80px;
    // padding-top: 40px;
    position: fixed;
    top: 0px;
    width: 100%;
    justify-content: space-between;
    @include flexbox();
    @include transition(all 0.2s);
    &.active{
        background-color: var(--cmain);
        padding-top: 12px;
        padding-bottom: 12px;
    }
    .menu {
        @include flexbox();

        li {
            padding: 0 21px;
        }

        a {
            color: var(--cwhite);
            line-height: 41px;
            display: inline-block;
            position: relative;
            font-size: 20px;
        }
    }

    .logo {
        max-width: 82px;
        margin-right: 20px;
        position: relative;

        img {
            width: 100%;
        }
    }

    .left {
        display: -webkit-inline-flex;
        display: -moz-inline-flex;
        display: inline-flex;
        align-items: center;
        .menu{
            a{
                &::before {
                    content: '';
                    position: absolute;
                    width: 0;
                    height: 2px;
                    left: 0;
                    bottom: 0;
                    background-color: var(--cwhite);
                    @include transition(all 0.2s);
                }
    
                &:hover::before {
                    width: 100%;
                }
            }
        }
    }

    .right {
        line-height: 41px;
        display: -webkit-inline-flex;
        display: -moz-inline-flex;
        display: inline-flex;
        align-items: center;
        // width: 30px;
        li{
            display: inline-block;
            position: relative;
            padding: 0;
            &:not(:last-child){
                margin-right: 30px;
            }
            a{
                white-space: nowrap;
                &:not(:last-child){
                    margin-right: 10px;
                }
            }
            &:hover {
                .link{
                    bottom: -38px;
                    opacity: 1;
                    pointer-events: unset;
                    a{
                        &:hover{
                            color: var(--cgray);
                        }
                    }
                }
                .icon{ 
                    svg {
                        path{
                            fill: var(--cmain);
                        }
                    }
                    
                }
            }
        }
        .link{
            opacity: 0;
            bottom: 0px;
            @include flexbox();
            position: absolute;
            right: 0;
            pointer-events: none;
            @include transition(all 0.2s);
            a{
                font-size: 14px;
            }
        }
        .icon{ 
            width: 30px;
            height: 30px;
            display: inline-block;
            cursor: pointer;
            z-index: 1;
            svg {
                width: 100%;
                path{
                    fill: white;
                }
            }
            
        }
    }

    //v2
    &.active{
        .logo {
            max-width: 48px;
            margin-right: 20px;
        }
        .left {
            padding-top: 0;
            .menu{
                padding-top: 0;
                a{
                    font-size: 14px;
                    &::before {
                        height: 2px;
                    }
                }
            }
        }
        .right{
            align-items: center;
            padding-top: 0;
            li{
                a{
                    font-size: 14px;
                }
            }
        }
    }
    .logo {
        max-width: 133px;
        margin-right: 64px;
    }
    .left {
        padding-top: 40px;
        .menu{
            padding-top: 30px;
            a{
                &::before {
                    height: 3px;
                }
            }
        }
    }
    .right{
        align-items: unset;
        padding-top: 40px;
        li{
            a{
                font-size: 14px;
                &:hover{
                    color: var(--chover);
                }
            }
        }
    }
    
    @media screen and (max-width: 1480px){
        .right li:not(:last-child) {
            margin-right: 0px;
        }
        .menu {
            a {
                font-size: 18px;
            }
            li {
                padding: 0 12px;
            }
        }
        .logo {
            margin-right: 24px;
            max-width: 80px;
        }
    }
    @media screen and (max-width: 1280px){
        padding: 0 30px;
        .menu {
            li {
                padding: 0 10px;
            }
        }
        .logo {
            margin-right: 18px;
        }
    }
}
