@media screen and (max-width: 767.98px){
    .home-block-6{
        padding: 30px;
        .sub-title{
            font-size: 12px;
        }
        .title{
            font-size: 30px;
        }
        .text{
            font-size: 16px;
        }
        .button{
            font-size: 12px;
            padding: 10px 20px;
            line-height: 20px;
            svg{
                width: 20px;
            }
        }
    }
    .home-block-5{
        .content{
            padding: 30px;
        }
        .title{
            font-size: 24px;
        }
    }
    .home-block-4{
        padding: 60px 0;
        .title{
            font-size: 30px;
        }
        .text{
            font-size: 16px;
        }
        .button{
            font-size: 16px;
            svg{
                width: 20px;
            }
        }
    }
    .home-block-3{
        padding: 60px 0;
        .btn-slick{
            svg{
                width: 20px;
            }
        }
        .title{
            font-size: 30px;
            margin-bottom: 0px;
        }
        p.text{
            margin-bottom: 30px;
            font-size: 18px;
        }
    }
    .home-block-2 {
        padding-bottom: 70px;
        padding-top: 30px;
        
        .right{
            .content{
                padding: 40px 30px;
                .title{
                    margin-bottom: 20px;
                    font-size: 30px;
                }
            }
        }
    }
    .home-banner{
        .content{
            padding: 0 30px;
            .title{
                font-size: 30px;
            }
            .sub-title{
                font-size: 16px;
            }
            .text{
                font-size: 16px;
            }
        }
        .form-search-top{
            #input-search-top{
                line-height: 40px;
                font-size: 12px;
                
            }
            #search-expand-top{
                .button-search-top{
                    padding: 6px 10px;
                    border-radius: 4px;
                }
                .title-search-top{
                    font-size: 12px;
                }
            }
            svg{
                width: 24px;
                top: 5px;
            }
        }
    }
    .article-detail{
        .text{
            font-size: 16px;
            line-height: 24px;
        }
        .text-img{
            font-size: 18px;
        }
        .title{
           font-size: 24px;
           line-height: 50px;
        }
        img{
            margin: 15px 0;
        }
        .btn-blog{
            margin-bottom: 10px;
            svg{
                width: 30px;
                min-width: 20px;
            }
            &.left{
                svg{
                    margin-left: 0;
                }
            }
        }
        .btn-bottom {
            .btn-blog{
                margin-top: 50px;
                margin-bottom: 0;
                svg{
                    width: 30px;
                    min-width: 20px;
                }
                &.right{
                    svg{
                        margin-right: 0;
                    }
                }
                &.left{
                    svg{
                        margin-left: 0;
                    }
                }
                img{
                    display: none;
                }
            }
        }
    }
    footer{
        padding: 13px;
        padding-top: 30px;
        .copy-right{
            padding-bottom: 15px;
            padding-top: 80px;
            position: relative;
            .left{
                width: 100%;
                text-align: center;
                padding-bottom: 30px;
                .logo{
                    top: -54px;
                    left: 50%;
                    transform: translateX(-50%);
                }
                p, .link{
                    padding-left: 0;
                }
            }
            .right{
                position: absolute;
                left: 50%;
                bottom: 15px;
                transform: translateX(-50%);
                a:first-child{
                    margin-left: 0;
                }
            }
            p, .link{
                font-size: 13px;
            }
        }
    }
    header{
        padding: 0 30px;
        .left{
            padding-top: 30px;
        }
        .right{
            padding-top: 20px;
            .btn-subscribe-header{
               position: absolute;
               top: 55px;
               margin-right: 0 !important;
               #btn-subscribe-header{
                padding: 5px 9px;
               }
            }
            li.th-lang{
                white-space: nowrap;
            }
            li{
                padding: 0;
            }
        }
        &.active{
            .right{
                .res-menu-icon{
                    display: block;
                    width: 24px;
                    cursor: pointer;
                }
                .menu{
                    display: none;
                    margin-top: -32px;
                    li{
                        padding: 0;
                    }
                }
                .btn-subscribe-header{
                   top: 32px;
                }
                
            }
            &.show-menu{
                padding-top: 18px;
                padding-bottom: 18px;
                .right{
                    .res-menu-icon{
                        display: none;
                    }
                    .menu{
                        @include flexbox();
                    }
                }
            }
        }
        
    }
    .banner-mid-coming{
        .content{
            padding: 0 30px;
        }
    }
    .form-comingsoon{
        .title, .text-sub{
            font-size: 24px;
        }
        .sub-title{
            font-size: 16px;
        }
        .text{
            font-size: 16px;
        }
        input, button{
            font-size: 14px;
        }
    }
    .article-list{
        padding: 30px 13px;
        .article{
            margin-bottom: 15px;
            .content{
                padding: 15px;
                box-shadow: 0px 4px 15px rgba(0, 0, 0, 0.15);
            }
        }
        .col-12:last-child{
            padding: 0 15px;
            .article{
                width: 100%;
                float: unset;
                padding: 0;
            }
        }
    }
}