.form-checkout {
    width: 100%;
    padding: 0 80px;
    padding-bottom: 60px;
    color: var(--cblack);

    .top-text {
        @include flexbox();
        justify-content: space-between;
        margin-top: 35px;

        .lefts {
            font-size: 20px;
            opacity: 0.7;
            margin-bottom: 23px;
            font-weight: normal;
            color: var(--cblack);
        }

        .right {
            font-size: 14px;

            svg {
                margin-right: 10px;
            }
        }
    }

    .card {
        background: #FFFFFF;
        box-shadow: 0px 0px 15px rgba(0, 0, 0, 0.2);
        border-radius: 5px;
        padding: 28px 21px;
        margin-bottom: 30px;
    }

    .container-fluid {
        .row {
            margin-right: -30px;
            margin-left: -30px;
        }
    }

    .title {
        font-weight: 800;
        font-size: 20px;
    }

    .sub-text {
        font-size: 14px;
        margin-top: 11px;

        a {
            color: #048FDD;
            margin-left: 3px;
            margin-right: 3px;
        }
    }

    .require {
        color: red;
    }

    .wrap-with-50 {
        width: 100%;

        .form-group {
            width: calc(50% - 8px);
            float: left;
        }

        .form-group:first-child {
            margin-right: 8px;
        }

        .form-group:last-child {
            margin-left: 8px;
        }
    }

    .img-credit {
        max-width: 690px;
        width: 100%;
        margin-bottom: 16px;
    }

    .credit .form-group {
        width: 50%;
        width: 300px;

        input {
            margin-bottom: 13px;
        }

        .wrap-with-50 {
            input {
                width: calc(50% - 8px);
                float: left;

                &:first-child {
                    margin-right: 8px;
                }

                &:last-child {
                    margin-left: 8px;
                }
            }

        }
    }

    .wrap-btn {
        width: 100%;
        text-align: center;
        margin-top: 40px;

        .btn-cancel {
            font-size: 18px;
            margin-right: 57px;
            text-transform: uppercase;
        }

        .btn-submit {
            text-transform: uppercase;
            color: var(--cwhite);
            padding: 10px 50px;
            background-color: var(--cmain);

            &:hover {
                background-color: rgb(87, 98, 119);
            }
        }
    }

    .type {
        width: 100%;
        background-color: var(--cblack);
        color: var(--cwhite);
        padding: 6px 12px;
        font-size: 14px;
        margin-top: 13px;
        margin-bottom: 20px;
    }

    .right-checkout-info {
        hr {
            margin: 0;
        }
    }

    .info-type {
        display: inline-block;
        margin-bottom: 13px;

        .left {
            width: 50%;
            float: left;
            display: inline-block;

            img {
                width: 100%;

            }
        }

        .info {
            display: inline-block;
            width: 50%;
            padding-left: 14px;

            .name {
                font-size: 14px;
                margin-bottom: 25px;
            }

            .star {
                color: rgba(4, 141, 221, 0.5);
                font-size: 14px;
                margin-bottom: 22px;

                .numb-star {
                    font-size: 8px;
                    padding: 4px 8px;
                    background-color: #2D9CDB;
                    border-radius: 8px;
                    color: var(--cwhite);
                    display: inline-block;
                    margin-right: 13px;

                    svg {
                        width: 9px;
                        height: 9px;
                        margin-top: -3px;

                        path {
                            fill: var(--cwhite);
                        }
                    }
                }

            }

            .location {
                font-size: 12px;
                opacity: 0.5;
                position: relative;
                padding-left: 28px;

                svg {
                    position: absolute;
                    left: 0;
                    top: 3px;
                }
            }
        }
    }

    .date-picker {
        position: relative;
        padding: 10px 13px 10px 60px;
        width: 100%;
        border: 1.10204px solid #ECEBED;
        box-shadow: 0px 7.71429px 70.5306px rgba(0, 0, 0, 0.07);
        border-radius: 6.61225px;
        margin-top: 13px;
        margin-bottom: 13px;

        span {
            width: 100%;
            display: block;
            color: #3F3356;
            opacity: 0.5;
        }

        svg {
            position: absolute;
            left: 18px;
            bottom: 14px;
        }

        input {
            background: transparent;
            box-shadow: none;
            border: none;
            width: 100%;
        }
    }

    .room-type {
        margin-top: 10px;
        font-weight: 500;
        font-size: 14px;
        @include flexbox();
        justify-content: space-between;

        a {
            margin-left: 13px;
            text-decoration: underline;
            color: #048FDD;
            opacity: 0.6;
        }
    }

    ul.info {
        font-size: 14px;
        line-height: 19px;
        color: var(--cblack);
        opacity: 0.6;
        padding: 9px;
    }

    .tax-info {
        font-size: 14px;
        line-height: 19px;
        color: #000000;
        @include flexbox();
        justify-content: space-between;

        p {
            opacity: 0.6;
        }

        .price {
            opacity: 1;
        }
    }

    .add-service {
        padding: 12px;
        font-size: 14px;
        line-height: 19px;
        color: #048FDD;
        opacity: 0.6;

        li:not(:last-child) {
            margin-bottom: 11px;
        }
    }

    textarea {
        resize: none;
        width: 100%;
        border: 0.754449px solid #ECEBED;
        padding: 10px 16px;
        border-radius: 4.52669px;
        font-size: 14px;
        line-height: 19px;
        color: var(--cblack);
    }

    .total-price {
        margin-top: 14px;
        font-weight: 800;
        font-size: 14px;
        @include flexbox();
        justify-content: space-between;
    }
}

.wrap-form-complete {
    text-align: center;
    background-color: #00000079;
    z-index: 1111;
    position: fixed;
    top: 0;
    left: -50%;
    width: 100vw;
    height: 100vh;
    opacity: 0;
    pointer-events: none;
    transition: all 0.3s;
    &.active{
        opacity: 1;
        left: 0;
        pointer-events: unset;
    }
    .form-complete {
        background-color: var(--cwhite);
        width: 50%;
        max-width: 770px;
        position: absolute;
        top: 20%;
        left: 50%;
        transform: translateX(-50%);

        .title {
            font-size: 15px;
            line-height: 20px;
            letter-spacing: 0.2em;
            text-transform: uppercase;
            padding: 25px 0 20px 0;
            border-bottom: 0.5px solid rgba(0, 0, 0, 0.8);
        }

        #btn-close-form-complete {
            cursor: pointer;
            position: absolute;
            top: 22px;
            right: 54px;

            svg {
                width: 21px;
            }
        }

        .text {
            font-weight: 500;
            font-size: 20px;
            line-height: 27px;
            color: var(--cblack);
            margin-bottom: 13px;
            margin-top: 70px;
        }
        a{
            text-decoration: underline;
            font-size: 14px;
            display: inline-block;
            margin-bottom: 13px;
        }
        .bottom{
            margin-bottom: 15px;
            margin-top: 60px;
        }
    }
}