@import 'mixins';

:root {
  --cmain: #414A5A;
  --cbg: #EEEEEE;
  --cgray: #CCCCCC;
  --cwhite: #FFFFFF;
  --chover: #8D8875;
  --cblack: #000000;
  --font: 'Avenir LT Std';
  --font-title: 'BanglaMN';
}


//Ti
.aq-main-content {
  overflow: hidden;
  position: relative;
}

header.active {
  &~.aq-main-content {
    padding-top: 68px;
  }
}

* {
  font-family: var(--font);

  &:focus {
    outline: none;
  }
}

body {
  background-color: var(--cbg);
}

.mb-0 {
  margin-bottom: 0;
}

dl,
ol,
ul {
  margin-top: 0;
  margin-bottom: 0;
  padding: 0;
  list-style-type: none;
}

a {
  color: unset;

  &:hover {
    color: #8D8875;
    text-decoration: none;
    @include transition(all 0.2s);
  }
}

p,
h1,
h2,
h3,
h4 {
  margin: 0;
}

.link-overlay,
.dark-overlay {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.link-overlay {
  z-index: 111;
}

.dark-overlay {
  background-color: rgba(0, 0, 0, 0.7);
}

.aq-main-content {
  &.has-overflow {
    &::before {
      content: '';
      background-color: rgba(0, 0, 0, 0.7);
      position: absolute;
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;
      z-index: 999;
    }
  }
}

.have-bg {
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.full-height {
  height: 100vh;
  max-height: 1080px;
  min-height: 720px;
}

.sub-title {
  font-size: 20px;
  text-transform: uppercase;
}

.title {
  font-size: 48px;
  font-family: var(--font-title);
}

.text {
  font-size: 20px;
}

.pr {
  position: relative;
}

.button {
  &:hover {
    color: unset;
  }
}

.user-form {
  opacity: 0;
  @include mid-center-content();
  left: -120%;
  position: fixed;
  z-index: 1000;
  width: 60vw;
  min-width: 700px;
  height: 70vh;
  min-height: 600px;
  background-color: var(--cwhite);
  border-radius: 10px;
  box-shadow: 0px 4px 25px rgba(0, 0, 0, 0.15);
  overflow: hidden;

  ::-webkit-input-placeholder {
    /* Edge */
    color: #CCCCCC;
  }

  :-ms-input-placeholder {
    /* Internet Explorer */
    color: #CCCCCC;
  }

  ::placeholder {
    color: #CCCCCC;
  }

  input {
    padding-top: 10px
  }

  .title {
    margin-bottom: 20px;
    font-size: 30px;
    line-height: 100%;
  }

  &.active {
    left: 50%;
    opacity: 1;
    @include transition(all 0.5s);
  }

  hr {
    margin-bottom: 50px;
  }

  .left,
  .right {
    width: 50%;
    float: left;
  }

  .left {
    padding: 32px;

    label {
      display: unset;
    }

    .login-one-step {
      a {
        display: inline-block;
        width: 100%;
        margin-bottom: 10px;
        padding: 10px 15px;
        text-align: center;

      }

      .gg-btn {
        border: 1px solid rgb(216, 216, 216);

        &:hover {
          background-color: rgba(216, 216, 216, 0.342);
          color: unset;
        }

        svg {
          width: 30px;
          height: 30px;
          margin-right: 10px;
        }

        span {
          line-height: 30px;
        }
      }

      .fb-btn {
        background-color: rgb(66, 103, 178);
        color: var(--cwhite);

        &:hover {
          background-color: rgb(80, 110, 172);
        }

        svg {
          width: 24px;
          height: 24px;
          margin-right: 10px;

          path {
            fill: var(--cwhite);
          }
        }

        span {
          line-height: 30px;
        }
      }
    }

    .aq-btn-login {
      width: 100%;
      text-align: center;
      background-color: var(--cmain);
      padding: 10px 15px;
      line-height: 30px;
      color: var(--cwhite);
    }
  }

  .right {
    overflow: hidden;
    height: 100%;

    img {
      object-position: 50% 50%;
      height: 100%;
      object-fit: none;
      object-position: 67% 27%;
    }
  }

  .btn-close {
    position: absolute;
    top: 22px;
    right: 22px;
    cursor: pointer;

    svg {
      path {
        fill: var(--cwhite);
      }
    }
  }
}

#user-register-form {
  min-height: 694px;

  .title {
    margin-bottom: 40px;
  }

  button {
    margin-top: 40px;
  }
}

.sign-up-bottom-wrap {
  padding: 0 80px;
}

.sign-up-bottom {
  background-color: var(--cmain);
  color: var(--white);
  text-align: center;
  padding-top: 50px;
  padding-bottom: 30px;
  margin-bottom: 70px;

  .title {
    font-size: 40px;
  }

  .text {
    font-size: 20px;
    margin-top: 10px;
  }

  .form-group {
    margin-top: 30px;
    @include flexbox();
    align-items: center;
    justify-content: center;

    input {
      width: 350px;
      padding: 13.5px;
      border: none;
    }

    .btn-form {
      font-size: 13px;
      background: #8D8875;
      border: none;
      color: var(--cwhite);
      padding: 15px;

      svg {
        margin-left: 10px;
        width: 20px;
        margin-top: -4px;
      }
    }
  }
}

.select-type {
  position: relative;
  border-bottom: 1px solid rgba(65, 74, 90, 0.3);
  display: inline-block;
  font-size: 12px;
  padding-bottom: 2px;
  width: 100%;
  padding-left: 0;
  margin-bottom: 21px;

  span {
    color: var(--chover);
    width: 100%;
    float: left;
    padding-left: 10px;
    margin-bottom: 2px;
    z-index: 11;
    position: relative;
    pointer-events: none;
  }

  select {
    background-color: transparent;
    border: none;
    padding: 0 10px;
    color: var(--cmain);
    -webkit-appearance: none;
    -moz-appearance: none;
    width: 100%;
    font-size: 13px;
  }

  svg,
  img {
    position: absolute;
    right: 10px;
    top: 8px;
    width: 22px;
  }

  &.with-icon {
    padding-left: 40px;

    svg,
    img {
      left: 10px;
    }
  }

  &.date-picker {
    input {
      border: none;
      padding-left: 10px;
      width: 190px;
      color: var(--cmain);
    }
  }
}
footer{
    .copy-right{
        .left{
            .logo{
                width: 23px;
                img{
                    width: 100%;
                }
            }
        }
    }
}

