@media screen and (max-width: 1199.98px){
    header{
        padding: 0 48px;
        .left{
            .menu{
                display: none;
            }
        }
        .right{
            padding-top: 30px;
            .menu{
                display: none;
            }
            .res-menu-icon{
                width: 30px;
                path{
                    fill: var(--cwhite);
                }
            }
        }
        &.coming-soon-header{
            .right{
                .menu{
                    display: flex;
                }
            }
        }
    }

    .form-comingsoon{
        padding: 48px;
    }
    .article-list{
        padding: 48px 33px;
        
    }
    .article{
        padding: 0;
    }
    .home-block-2 {
        padding-bottom: 180px;
        .img-left{
            width: 100%;
        }
        .right{
            right: 0;
        }
        .content{
            min-height: 570px;
            padding-left: 80px;
            padding-bottom: 100px;
            .sub-title{
                font-size: 14px;
            }
            .title{
                font-size: 36px;
            }
            .text{
                font-size: 16px;
                margin-bottom: 50px;
            }
        }
    }
}