@media screen and (max-width: 374.98px){
    .home-block-5{
        .sub-form{
            .button{
                width: unset;
                span{
                    display: none;
                }
                svg{
                    margin-left: 0;
                }
            }
        }
       
    }
    .home-block-2 {
        .right{
            .content{
                // padding: 15px;
                // padding-top: 30px;
                .sub-title{
                    font-size: 12px;
                }
                .title{
                    font-size: 24px;
                    line-height: 30px;
                }
            }
        }
    }
    .form-comingsoon{
        input{
            font-size: 12px;
            padding: 5px 10px;
        }
        button{
            padding: 0 10px;
            font-size: 12px;
        }
    }
}
