@media screen and (max-width: 991.98px){
    .home-block-6{
        padding: 48px;
    }
    .home-block-4{
        padding: 80px 0;
        .title{
            font-size: 36px;
            margin-bottom: 10px;
        }
        .button{
            margin-bottom: 30px;
        }
    }
    .home-block-3{
        padding: 60px 0;
        .title{
            font-size: 36px;
            margin-bottom: 10px;
        }
        p.text{
            margin-bottom: 60px;
            font-size: 18px;
        }
    }
    .home-block-2 {
        padding-bottom: 100px;
        padding-top: 60px;
        .pr{
            z-index: 0;
        }
        .right{
            position: relative;
            margin-top: -36px;
            .content{
                min-height: unset;
                padding: 60px;
                .sub-title{
                    margin-bottom: 20px;
                }
            }
        }
    }
    .home-banner{
        .content{
            width: 100%;
            max-width: unset;
            padding: 0 80px;
            .title{
                font-size: 36px;
                margin-bottom: 10px;
            }
            .sub-title{
                font-size: 18px;
                letter-spacing: 1px;
            }
            .text{
                font-size: 18px;
            }
        }
    }
    .article-detail{
        padding: 33px;
        .text, .title, .text-img{
            padding: 0;
        }
        img{
            width: 100%;
        }
    }
    footer{
        padding: 33px;
        padding-bottom: 15px;
        .copy-right{
            padding-top: 15px;
            padding-bottom: 0;
            .left{
                display: block;
                position: relative;
                .logo{
                    position: absolute;
                    top: 2px;
                    width: 46px;
                }
                .link{
                    width: 100%;
                    margin-top: 4px;
                    margin-left: 0;
                    padding-left: 54px;
                }
                p{
                    padding-left: 54px;
                }
            }
        }
    }
    .banner-mid-coming{
        .content{
            padding: 0 80px;
            .sub-title{
                font-size: 18px;
            }
            .title{
                font-size: 36px;
                line-height: 36px;
            }
        }
    }
    .coming-soon-banner {
        .content {
            padding: 0 15px;
            .title{
                font-size: 36px;
            }
            .text{
                font-size: 30px;
            }
        }
    }
    .form-comingsoon{
        .title, .text-sub{
            font-size: 36px;
        }
        .sub-title{
            font-size: 18px;
        }
        .text{
            font-size: 18px;
        }
        input, button{
            font-size: 16px;
        }
    }
    .article-list{
        .col-12:last-child{
            padding: 0;
            -ms-flex: 0 0 100%;
            flex: 0 0 100%;
            max-width: 100%;
            .article{
                width: 50%;
                float: left;
                padding: 15px;
            }
        }
    }
}