@import 'includes/master/mixins';

.home-banner{
    position: relative;
    height: 100vh;
    width: 100%;
    &::before{
        content: '';
        background: linear-gradient(180deg, rgba(0, 0, 0, 0.6) 1.58%, rgba(0, 0, 0, 0) 31.82%, rgba(17, 16, 16, 0.51) 51.35%);
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        z-index: 0;
    }
    .content{
        color: var(--cwhite);
        text-align: center;
        @include mid-content();
        z-index: 1;
        bottom: 80px;
        max-width: 700px;
        .sub-title{
            margin-bottom: 10px;
        }
        .title{
            margin-bottom: 24px;
        }
        .text{
            margin-bottom: 60px;
        }
        
    }
    .form-search-top{
        width: 100%;
        margin: 0 auto;
        max-width: 644px;
        position: relative;
        display: inline-block;
        svg{
            position: absolute;
            left: 14px;
            top: 11px;
            opacity: 0.5;
        }
        #input-search-top{
            width: 100%;
            line-height: 50px;
            font-size: 14px;
            padding: 0 55px;
            border: none;
            
        }
        #search-expand-top{
            color: var(--cblack);
            background-color: var(--cwhite);
            text-align: left;
            position: absolute;
            padding: 18px 24px;
            width: 100%;
            box-shadow: 0px 4px 25px rgba(0, 0, 0, 0.15);
            opacity: 0;
            @include transition(all 0.3s);
            &.active{
                opacity: 1;
            }
            .title-search-top{
                font-size: 14px;
                opacity: 0.8;
            }
            .list-link{
                display: inline-block;
            }
            .button-search-top{
                font-size: 14px;
                opacity: 0.5;
                border: 0.5px solid #000000;
                border-radius: 10px;
                padding: 10px 18px;
                margin-right: 10px;
                margin-top: 12px;
                float: left;
                &:hover{
                    opacity: 1;
                    box-shadow: 0px 2px 15px rgba(0, 0, 0, 0.15);
                }
            }
        }
    }
    // .form-expand-search-top{
    //     color: var(--cblack);
    //     background-color: var(--cwhite);
    //     text-align: left;
    //     position: relative;
    //     #search-expand-top{
    //         padding: 18px 24px;
    //         position: absolute;
    //         // box-shadow: 0px -2px 24px rgba(0, 0, 0, 0.26);
    //         box-shadow: 0px 4px 25px rgba(0, 0, 0, 0.15);
    //     }
    //    
    //     .title-search-top{
    //         font-size: 14px;
    //         opacity: 0.8;
    //     }
    //     .button-search-top{
    //         font-size: 14px;
    //         opacity: 0.5;
    //     }
    // }
    
}

.home-block-2{
    position: relative;
    padding-top: 90px;
    padding-bottom: 90px;
    overflow: hidden;
    width: 100%;
    .img-bg{
        position: absolute;
        top: -10%;
        left: 0;
    }
    .img-left{
        box-shadow: 0px 0px 50px #0000003a;
    }
    .content{
        background-color: var(--cmain);
        width: 100%;
        height: auto;
        min-height: 670px;
        padding-left: 46%;
        padding-right: 6%;
        padding-top: 80px;
        color: var(--cwhite);
        .line{
            width: 320px;
            height: 2px;
            background-color: var(--cwhite);
            position: absolute;
            right: -80px;
        }
        .sub-title{
            font-size: 15px;
            margin-bottom: 10px;
        }
        .title{
            font-size: 40px;
            line-height: 40px;
            margin-bottom: 60px;
        }
        .text{
            padding-top: 20px;
            font-size: 20px;
            margin-bottom: 20px;
        }
        .button{
            svg{
                margin-top: -5px;
                margin-left: 10px;
                @include transition(all 0.2s);
            }
            &:hover{
                svg{
                    margin-left: 20px;
                }
            }
        }
    }
    .left{
        z-index: 1;
    }
    .right{
        position: absolute;
        right: 8.3%;
        top: 35px;
        z-index: 0;
    }
}

.home-block-3{
    background-color: var(--cmain);
    position: relative;
    padding: 90px 0px;
    color: var(--cwhite);
    .img-bg-1{
        position: absolute;
        top: 0;
        left: 20%;
    }
    .img-bg-2{
        position: absolute;
        bottom: 0;
        right: 20%;
    }
    .title{
        font-size: 40px;
        margin-bottom: 20px;
    }
    p.text{
        margin-bottom: 67px;
        font-size: 20px;
    }
    .wrap-slide-home{
        position: relative;
        margin-left: -20px;
        margin-right: -20px;
        .slick-arrow{
            display: none !important;
        }
        .btn-prev, .btn-next{
            position: absolute;
            top: 50%;
            transform: translateY(-50%);
            cursor: pointer;
        }
        .btn-prev{
            left: -30px;
        }
        .btn-next{
            right: -30px;
        }
    }
    .home-block-3-slide-img{
        
        .slide-img{
            position: relative;
            display: inline-block;
            padding: 0 20px;
            img{
                width: 100%;
            }
            &::before{
                content: '';
                background: linear-gradient(0deg, rgba(0, 0, 0, 0.6) 39.73%, rgba(196, 196, 196, 0.054) 98.93%);
                position: absolute;
                top: 0;
                bottom: 0;
                left: 20px;
                right: 20px;
                z-index: 0;
            }
            .text{
                position: absolute;
                z-index: 1;
                bottom: 56px;
                left: 40px;
                width: calc(100% - 20px);
                .line{
                    width: 80%;
                    height: 2px;
                    background-color: var(--cwhite);
                    margin-bottom: 10px;
                }
            }
        }
    }
}

.home-block-4{
    padding: 130px 0;
    img{
        width: 100%;
    }
    .sub-title{
        font-size: 15px;
        margin-bottom: 10px;
        color: var(--cmain);
    }
    .title{
        font-size: 40px;
        color: var(--cmain);
        margin-bottom: 30px;
    }
    .text{
        font-size: 20px;
        margin-bottom: 30px;
        color: var(--cmain);
    }
    .button{
        font-size: 20px;
        color: var(--cmain);
        display: block;
        margin-bottom: 20px;
        svg{
            margin-top: -5px;
            margin-left: 10px;
            @include transition(all 0.2s);
            path{
                fill: var(--cmain);
            }
        }
        &:hover{
            svg{
                margin-left: 20px;
            }
        }
    }
}

.home-block-5{
    img{
        width: 100%;
    }
    .content{
        padding: 55px 40px;
        @include transform(translate(0, -50%));
        color: var(--cwhite);
        background-color: var(--cmain);
    }
    .title{
        font-size: 40px;
        margin-bottom: 10px;
    }
    .text{
        font-size: 15px;
        margin-bottom: 20px;
    }
    .sub-form{
        @include flexbox();
        input{
            width: 100%;
            line-height: 50px;
            font-size: 14px;
            padding: 0 14px;
            border: none;
        }
        .button{
            width: 143px;
            line-height: 50px;
            white-space: nowrap;
            padding: 0 20px;
            font-size: 14px;
            background-color: #8D8875;
            svg{
                width: 20px;
                margin-top: -5px;
                margin-left: 10px;
                @include transition(all 0.2s);
            }
            &:hover{
                svg{
                    margin-left: 20px;
                }
            }
        }
    }
}

.home-block-6{
    padding: 77px 80px;
    color: var(--cwhite);
    position: relative;
    &::before{
        content: '';
        background: linear-gradient(140.97deg, rgba(0, 0, 0, 0.66) 34.85%, rgba(0, 0, 0, 0) 52.13%);
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        z-index: 0;
    }
    .sub-title{
        font-size: 15px;
        opacity: 0.5;
        letter-spacing: 0.2em;
        margin-bottom: 20px;
    }
    .title{
        font-size: 40px;
        margin-bottom: 30px;
        max-width: 676px;
    }
    .text{
        opacity: 0.7;
        font-size: 20px;
    }
    .button{
        margin-top: 40px;
        text-transform: uppercase;
        line-height: 50px;
        white-space: nowrap;
        padding: 0 20px;
        display: inline-block;
        font-size: 14px;
        background-color: #8D8875;
        svg{
            margin-top: -5px;
            margin-left: 10px;
            @include transition(all 0.2s);
        }
        &:hover{
            svg{
                margin-left: 20px;
            }
        }
    }
}