@font-face {
    font-family: 'Avenir LT Std';
    src: url('../fonts/AvenirLTStd-Medium.eot');
    src: url('../fonts/AvenirLTStd-Medium.eot?#iefix') format('embedded-opentype'),
        url('../fonts/AvenirLTStd-Medium.woff2') format('woff2'),
        url('../fonts/AvenirLTStd-Medium.woff') format('woff'),
        url('../fonts/AvenirLTStd-Medium.ttf') format('truetype');
    font-weight: 500;
    font-style: normal;
}

@font-face {
    font-family: 'Avenir LT Std';
    src: url('../fonts/AvenirLTStd-Book.eot');
    src: url('../fonts/AvenirLTStd-Book.eot?#iefix') format('embedded-opentype'),
        url('../fonts/AvenirLTStd-Book.woff2') format('woff2'),
        url('../fonts/AvenirLTStd-Book.woff') format('woff'),
        url('../fonts/AvenirLTStd-Book.ttf') format('truetype');
    font-weight: normal;
    font-style: normal;
}


@font-face {
    font-family: 'BanglaMN';
    src: url('../fonts/BanglaMN.eot') format('embedded-opentype');
    font-weight: normal;
    font-style: normal;
}
  
@font-face {
    font-family: 'BanglaMN';
    src:  url(data:application/x-font-woff;charset=utf-8;base64,) format('woff'), url('../fonts/BanglaMN.ttf')  format('truetype'), url('BanglaMN.svg#BanglaMN') format('svg');
    font-weight: normal;
    font-style: normal;
}
  
  