.sort-dining {
    background-color: transparent;
    padding-top: 50px;
    padding-bottom: 50px;

    .total-offers {
        font-family: var(--font-title);
    }

    select,
    input {
        background-color: transparent;
    }
}

.dining-list {
    padding-top: 0;

    .article-product .content .left .text {
        max-width: 206px;
    }

    .bottom {
        ul {
            display: inline-flex;
            align-items: center;
        }

        li {
            padding: 0 7px;

            a {
                font-weight: 500;
                font-size: 24px;
                color: #9D9D9D;

                &:hover,
                &.active {
                    color: var(--cmain)
                }
            }
        }

        .btn-left,
        .btn-right {
            width: 63px;
            height: 63px;
            display: inline-block;
            line-height: 63px;
            text-align: center;
            border-radius: 40px;
            background-color: rgba(224, 224, 224, 0.3);

            &:hover {
                background-color: var(--cmain);

                svg {
                    path {
                        fill: var(--cwhite);
                    }
                }
            }

            svg {
                path {
                    fill: #2C2C2C;
                }
            }
        }

        .btn-left {
            margin-right: 20px;
        }

        .btn-right {
            margin-left: 20px;
        }
    }
}

.article-product-wrap,
.sub-yatch-info {
    .total-star {
        font-size: 12px;
        @include flexbox();
        align-items: center;

        .star {
            @include flexbox();
            width: 70px;
            margin-left: 5px;
            margin-right: 5px;

            svg {
                margin-top: -4px;
            }
        }
    }
}

.subscribe-dining {
    margin-top: 50px;
}

.banner-bot-dining {
    color: var(--cmain);

    .content {
        max-width: 600px;
    }

    &::before {
        background: transparent;
    }

    .button {
        color: var(--cmain);
        padding: 0;
        background-color: transparent;
        font-size: 20px;

        svg {
            width: 20px;

            path {
                fill: var(--cmain);
            }
        }
    }
}

.banner-form-booking {
    height: 635px;
    position: relative;
    margin-bottom: 170px;

    &::before {
        content: '';
        background: linear-gradient(123.21deg, rgba(0, 0, 0, 0.66) 34.85%, rgba(0, 0, 0, 0) 52.13%);
        position: absolute;
        pointer-events: none;
        left: 0;
        right: 0;
        top: 0;
        bottom: 0;
    }

    .content {
        position: absolute;
        left: 180px;
        top: 50%;
        transform: translateY(-50%);
        color: var(--cwhite);
        max-width: 450px;

        .sub-title {
            font-size: 15px;
            letter-spacing: 0.2em;
            margin-bottom: 10px;
        }

        .title {
            font-size: 40px;
            line-height: 55px;
            margin-bottom: 40px;
        }

        .text {
            margin-bottom: 20px;
        }

        .button {
            letter-spacing: 0.2em;

            &:hover {
                svg {
                    transform: translateX(5px);
                }
            }

            svg {
                transition: all 0.2s;
                width: 20px;
                margin-top: -5px;
                margin-left: 10px;
            }
        }
    }

    .form-right {
        background-color: var(--cwhite);
        box-shadow: 0px -2px 24px rgba(0, 0, 0, 0.26);
        padding: 43px 34px;
        display: inline-block;
        position: absolute;
        right: 180px;
        bottom: -100px;
        width: 370px;
        z-index: 11;

        .form-trip {
            display: inline-flex;
            align-items: center;

            .radio {
                margin-right: 32px;

                input {
                    margin-right: 6px;
                }
            }
        }

        .title {
            font-size: 20px;
            line-height: 25px;
            margin-bottom: 43px;
        }

        input,
        select {
            font-size: 15px;
        }

        .button {
            color: var(--cwhite);
            padding: 16px 30px;
            background-color: var(--cmain);
            float: right;
            display: block;

            &:hover {
                svg {
                    transform: translateX(5px);
                }
            }

            svg {
                transition: all 0.2s;
                width: 20px;
                margin-top: -5px;
                margin-left: 10px;
            }
        }
    }
}

.sub-dining-info {
    .sub-text {
        font-size: 12px;
        line-height: 16px;
        margin-bottom: 10px;
        opacity: 0.8;
    }

    .text {
        font-size: 14px;
        opacity: 1;

        strong {
            margin-right: 3px;
        }
    }

    .total-star {
        margin-top: 10px;
    }
}

.block-menu-dining {
    .menu-wrap {
        padding: 43px;
        background-color: var(--cmain);
        border-radius: 10px;
        color: var(--cwhite);
    }

    .title-menu {
        font-weight: 800;
        font-size: 24px;
        font-weight: normal;
        margin-bottom: 36px;
        text-align: center;
        
        strong {
            font-weight: 800;
        }
    }

    .block-menu {
        margin-bottom: 36px;
        width: 100%;
        display: inline-block;

        &.one-column {
            ul {
                margin: 0 auto;
                width: 50%;
            }
        }

        &.two-column {
            ul {
                width: 50%;
                float: left;
                padding-right: 40px;
            }
            ul:last-child{
                padding-left: 40px;
                padding-right: 0;
            }
        }
    }

    .title-menu,
    .name {
        text-transform: uppercase;
    }

    .name {
        font-size: 16px;
        line-height: 25px;
    }

    .des {
        font-size: 14px;
        line-height: 25px;
    }
    .price{
        font-size: 16px;
    }
    li{
        border-bottom: 1px dashed var(--cwhite);
        padding: 13px 0;
        margin-bottom: 13px;
        @include flexbox();
        justify-content: space-between;
    }
}
.block-location-dining{
    font-size: 20px;
    svg{
        margin-top: -3px;
        margin-right: 4px;
        path{
            fill: var(--chover);
        }
    }
    a{
        color: var(--chover);
    }
    iframe{
        width: 100%;
        height: 550px;
        margin-top: 20px;
    }
}