.filter-yatch{
    form{
        @include flexbox();
        align-items: center;
        justify-content: center;
        padding-top: 30px;
        padding-bottom: 22px;
    }
    .form-item{
        position: relative;
        border: none;
        border-bottom: 1px solid rgba(65, 74, 90, 0.3);
        display: inline-block;
        height: 50px;
        span{
            width: 100%;
            float: left;
            display: inline-block;
            color: #8D8875;
            font-size: 12px;
        }
    }
    .location{
        padding-left: 45px;
        display: inline-table;
        margin-right: 13px;
        .select2-container{
            min-width: 150px;
        }
        .select2-container--default .select2-selection--multiple{
            border: none;
            background-color: transparent;
        }
        .select2-selection__choice__remove{
            position: absolute;
            right: 12px;
            // top: 10px;
            color: var(--cwhite);
            background-color: var(--cmain);
            border-radius: 10px;
            line-height: 18px;
            text-align: center;
            width: 18px;
            height: 18px;
            &:hover{
                color: var(--chover);
            }
        }
        .select2-container--default .select2-selection--multiple .select2-selection__rendered {
            // padding: 0;
            padding-top: 16px;
            li{
                font-size: 13px;
                margin: 0;
                padding-left: 10px;
                padding-right: 10px;
                padding-bottom: 7px;
                background-color: transparent;
                width: 100%;
                border: none;
                &.select2-search--inline{
                    width: 0;
                    padding: 0;
                }
            }
        }
        svg{
            opacity: 0.5;
            width: 32px;
            position: absolute;
            left: 12px;
            top: 10px;
            z-index: 1;
        }
    }
    .date-picker{
        padding-left: 60px;
        margin-right: 13px;
        width: 250px;
        svg{
            position: absolute;
            left: 18px;
            top: 6px;
        }
        input{
            background: transparent;
            box-shadow: none;
            border: none;
            width: 190px;
        }
    }
    .total-guests{
        padding-left: 50px;
        svg{
            position: absolute;
            left: 12px;
            top: 10px;
        }
        select{
            border: none;
            background-color: var(--cbg);
            -webkit-appearance: none;
            -moz-appearance: none;
            padding: 0 10px;
            margin-left: -10px;
        }
    }
    .btn-search-filter{
        background-color: var(--cmain);
        padding: 15px 20px;
        color: var(--cwhite);
        border: none;
        margin-left: 13px;
        svg{
            margin-left: 10px;
            transition: all 0.2s;
        }
        &:hover{
            svg{
                transform: translateX(5px);
            }
        }
    }
}

.yatch-list-banner{
    height: 378px;
    color: var(--cwhite);
    position: relative;
    .content{
        position: absolute;
        top: 90px;
        left: 80px;
        max-width: 750px;
    }
    .title{
        font-size: 40px;
        margin-bottom: 20px;
    }
    .text{
        font-size: 18px;
    }
}

.sort-yatch{
    @include flexbox();
    justify-content: space-between;
    padding: 25px 80px;
    width: 100%;
    background-color: var(--cwhite);
    .total-offers{
        font-size: 20px;
        color: var(--cblack);
        line-height: 42px;
    }
    .right{
        width: 40%;
        @include flexbox();
        align-items: center;
    }
    .filter-type{
        position: relative;
        border-bottom: 1px solid rgba(65, 74, 90, 0.3);
        display: inline-block;
        font-size: 12px;
        width: 50%;
        padding-bottom: 2px;
        span{
            color: var(--chover);
            width: 100%;
            float: left;
            padding-left: 10px;
            margin-bottom: 2px;
            z-index: 11;
            position: relative;
            pointer-events: none;
        }
        select{
            background-color: transparent;
            border: none;
            padding: 0 10px;
            color: var(--cmain);
            -webkit-appearance: none;
            -moz-appearance: none;
            width: 100%;
            font-size: 13px;
            padding-top: 20px;
            margin-top: -20px;
        }
        svg{
            position: absolute;
            right: 10px;
            top: 8px;
        }
    }
    .search-name{
        position: relative;
        border-bottom: 1px solid rgba(65, 74, 90, 0.3);
        padding: 6px 10px;
        margin-left: 20px;
        width: 50%;
        input{
            border: none;
            font-size: 16px;
            width: 200px;
        }
        svg{
            opacity: 0.5;
            width: 16px;
        }
    }
}
.yatch-list{
    padding: 50px 40px;
    .bottom{
        width: 100%;
        text-align: center;
        .btn-more{
            margin: 0 auto;
            text-align: center;
            display: inline-block;
            padding: 15px 50px;
            background-color: var(--cmain);
            color: var(--cwhite);
            border-radius: 4px;
            text-transform: uppercase;
            font-size: 13px;
            &:hover{
                background-color:rgb(88, 100, 119);
            }
        }
        .text{
            margin-top: 60px;
            font-size: 36px;
            color: var(--cmain);
        }
        .sub-text{
            font-size: 18px;
            color: var(--cmain);
            margin-bottom: 30px;
        }
    }
    
}
.article-product-wrap{
    padding: 0 20px;
}
.article-product{
    border-radius: 4px;
    overflow: hidden;
    transition: all 0.3s;
    margin-bottom: 70px;
    position: relative;
    &:hover{
        box-shadow: 0px 4px 15px rgba(0, 0, 0, 0.25);
    }
    .img-block{
        position: relative;
        display: block;
        height: 260px;
        &::before{
            content: '';
            position: absolute;
            left: 0;
            top: 0;
            width: 100%;
            height: 50%;
            background: linear-gradient(180deg, #000000 0%, rgba(0, 0, 0, 0) 100%);
        }
        &::after{
            content: '';
            position: absolute;
            left: 0;
            bottom: 0;
            width: 100%;
            height: 50%;
            background: linear-gradient(0deg, #000000 0%, rgba(0, 0, 0, 0) 100%);
        }
        img{
            width: 100%;
        }
        .title{
            z-index: 1;
            font-size: 20px;
            color: var(--cwhite);
            position: absolute;
            left: 20px;
            padding-right: 20px;
            bottom: 20px;
        }
    }
    .content{
        background-color: var(--cwhite);
        position: relative;
        padding: 20px;
        padding-bottom: 50px;
        color: var(--cmain);;
        @include flexbox();
        justify-content: space-between;
        min-height: 214px;
        .btn-more{
            position: absolute;
            right: 20px;
            bottom: 20px;
            font-size: 12px;
            svg{
                margin-top: -5px;
                width: 20px;
                margin-left: 10px;
                transition: all 0.3s;
                path{
                    fill: var(--cmain);
                }
            }
            &:hover{
                svg{
                    transform: translateX(5px);
                }
            }
        }
        .left{
            width: 70%;
            .info-wrap{
                margin-top: 16px;
            }
            .type{
                margin-bottom: 8px;
                font-size: 14px
            }
            .text, .info{
                font-size: 12px;
                opacity: 0.8;
            }
            .info{
                display: inline-block;
            }
        }
        .right{
            text-align: right;
            span{
                font-size: 14px;
                color: rgba(0, 0, 0, 0.5);
            }
            .price{
                font-size: 20px;
            }
        }
    }
}

.yatch-detail-banner{
    color: var(--cwhite);
    height: 630px;
    overflow: hidden;
    background-size: contain;
    position: relative;
    .title-block{
        background-color: var(--cmain);
        @include flexbox();
        padding: 0 80px;
        
        a{
            border-right: 1px solid #ffffff4f;
            width: 220px;
            font-size: 14px;
            padding: 16px 0;
            padding-top: 20px;
            svg{
                margin-right: 10px;
               margin-top: -4px;
            }
        }
        .title{ 
            padding: 16px 0;
            padding-top: 20px;
            font-size: 20px;
            width: calc(100% - 22px);
            text-align: center;
        }
    }
    .slide{
        img{
            width: 100vw;
        }
    }
    .btn-slick{
        position: absolute;
        bottom: 30px;
        right: 30px;
        display: flex;
        .icon-image{
            margin-right: 14px;
            padding-top: 4px;
        }
        .btn-prev{
            cursor: pointer;
            margin-right: 15px;
        }
        .btn-next{
            cursor: pointer;
            width: 40px;
            svg{
                width: 100%;
            }
        }
    }
}

.sub-yatch-info{
    padding: 20px;
    background-color: var(--cwhite);
    color: var(--cmain);
    position: relative;
    width: calc(100% - 400px);
    margin-left: 80px;
    transform: translateY(-70%);
    box-shadow: 0px -1px 34px rgba(0, 0, 0, 0.15);
    .top{
        @include flexbox();
        justify-content: space-between;
        width: 100%;
        
    }
    .bottom{
        @include flexbox();
        justify-content: flex-end;
        width: 100%;
        align-items: flex-end;
    }
    .title{
        font-size: 20px;
        text-transform: uppercase;
    }
    .text{
        font-size: 12px;
        opacity: 0.8;
    }
    .info-wrap{
        display: inline-block;
        margin-top: 10px;
        li{
            font-size: 12px;
            float: left;
            display: inline-block;
        }
    }
    .right{
        text-align: right;
        .btn-add-favorite{
            span{
                font-size: 14px;
                margin-left: 4px;
            }
        }
    }
    .count-guest{
        @include flexbox();
        display: inline-flex;
        align-items: center;
        border-bottom: 1px solid rgba(65, 74, 90, 0.3);
        padding: 0 15px;
        height: 50px;
        .text{
            color: #8D8875;
            font-size: 18px;
            margin-right: 10px;
        }
        .count-numb{
            line-height: 24px;
            margin-left: 10px;
            margin-right: 10px;
            padding-top: 5px;
        }
    }
    .from-date-picker{
        margin-left: 13px;
        position: relative;
        padding-left: 56px;
        border-bottom: 1px solid rgba(65, 74, 90, 0.3);
        width: 250px;
        height: 50px;
        span{
            color: #8D8875;
            font-size: 12px;
            width: 100%;
            float: left;
        }
        svg{
            position: absolute;
            left: 15px;
            top: 5px;
        }
        input{
            border: none;
        }
    }
    .btn-wrap{
        margin-left: 13px;
    }
    .btn-book{
        padding: 15px 20px;
        background-color: var(--cmain);
        color: var(--cwhite);
        @include flexbox();
        width: 100%;
        min-width: 220px;
        span{
            white-space: nowrap;
        }
        svg{
            margin-left: 10px;
            width: 20px;
            transition: all 0.3s;
        }
        &:hover{
            svg{
                transform: translateX(5px);
            }
            
        }
    }
}
.content-detail-yatch {
    color: var(--cmain);
    padding: 0 80px;
    .title{
        font-size: 20px;
        margin-top: 30px;
        margin-bottom: 16px;
    }
    .sub-title{
        font-size: 20px;
        font-weight: bold;
        text-transform: unset;
        margin-bottom: 11px;
    }
    .text{
        font-size: 20px;
        line-height: 27px;
        margin-bottom: 40px;
    }
}
.body-content-detail-yatch{
    margin-top: -120px;
    .menu{
        display: inline-block;
        border-bottom: 0.5px solid #8D8875;
    }
    li{
        font-size: 14px;
        float: left;
        display: inline-block;
        padding: 4px 13px;
        &:hover{
            border-bottom: 2px solid var(--cmain);
        }
    }
}

.block-details, .block-amenities{
    width: 100%;
    margin-left: -15px;
    margin-right: -15px;
    font-size: 20px;
    .title{
        padding-left: 15px;
    }
}
.block-amenities{
    white-space: nowrap;
    svg{
        margin-top: -4px;
        margin-right: 9px;
    }
}

.block-travel{
    .total-star{
        font-size: 30px;
        @include flexbox();
        align-items: center;
        overflow: hidden;
        position: relative;
        width: 173px;
    }
    .star{
        margin-left: 10px;
        display: block;
        white-space: nowrap;
        svg{
            margin-top: -10px;
        }
    }
    .body-review{
        
        background-color: var(--cwhite);
        padding: 30px;
        margin-bottom: 80px;
        .review{
            @include flexbox();
            width: 100%;
            
            &:not(:last-child){
                margin-bottom: 30px;
                border-bottom: 1px solid rgba(65, 74, 90, 0.2);
                padding-bottom: 30px;
            }
        }
        .info{
            width: 30%;
            
            .top{
                @include flexbox();
                align-items: center;
                width: 100%;
                float: left;
                margin-bottom: 15px;
                .avatar{
                    display: inline-block;
                    width: 50px;
                    margin-right: 15px;
                    img{
                        width: 100%;
                    }
                    float: left;
                }
                .info-user{
                    width: 100%;
                    .name{
                        font-size: 16px;
                        font-weight: bold;
                    }
                    .country{
                        font-size: 13px;
                        svg{
                            width: 20px;
                            margin-right: 10px;
                        }
                    }
                }
            }
            .bottom{
                width: 100%;
                font-size: 13px;
                color: rgba(65, 74, 90, 0.5);
                svg{
                    width: 10px;
                    margin-right: 10px;
                }
            }
        }
        .text-review{
            width: 70%;
            .total-star{
                font-size: 24px;
                color: var(--cmain);
                font-weight: bold;
            }
            .small{
                font-size: 13px;
                color: var(--cgray);
                margin-right: 13px;
            }
            .text{
                font-size: 16px;
                line-height: 20px;
                margin-bottom: 0;
                color: rgba(65, 74, 90, 0.8);
            }
        }
        
    }
}


.block-similar{
    .wrap-article{
        margin-left: -45px;
        margin-right: -45px;
    }
    .article-product-wrap{
        .type{

        }
        .text{
            font-size: 12px;
            line-height: 16px;
            margin-bottom: 10px;
        }
        span{
            line-height: 100%;
        }
        
        .btn-more{
            svg{
                width: 25px;
                margin-top: -4px;
            }
        }
    }
}