
@media screen and (max-width: 479.98px){
    .title{
        font-size: 24px !important;
    }
    .sub-title{
        font-size: 12px !important;;
    }
    .text, .button, p.text{
        font-size: 16px !important;;
    }
    .home-block-6{
        padding: 30px 15px;
        .button{
            padding: 10px;
            font-size: 12px !important;;
        }
    }
    #comingsoonPage{
        .text{
            font-size: 12px !important;
        }
        .title{
            font-size: 20px !important;
        }
    }
    .home-block-5{
        margin-bottom: -98px;
        img{
            opacity: 0 !important;
        }
        .container{
            padding: 0;
        }
        .content{
            padding: 30px 15px;
        }
       
        .sub-form{
            input{
                font-size: 12px;
                line-height: 40px;
            }
            .button{
                line-height: 40px;
            }
        }
        
    }
    .home-block-4, .home-block-3{
        padding: 30px 0;
    }
    .home-block-2 {
        padding-bottom: 55px;
        padding-top: 15px;
        .right{
            .content{
                padding: 30px 15px;
                .sub-title{
                    margin-bottom: 10px;
                }
                .title{
                    font-size: 24px;
                }
            }
        }
    }
    .home-banner{
        .content{
            padding: 0 15px;
        }
    }
    header{
        padding-left: 15px;
        padding-right: 15px;
        .left{
            padding-top: 15px;
        }
        .coming-soon-header{
            .right{
                padding-top: 5px;
                .btn-subscribe-header{
                    top: 40px;
                }
            }
        }

    }
    footer{
        padding: 0;
        padding-top: 30px;
        text-align: center;
        .title-link{
            font-size: 14px;
            margin-bottom: 10px;
            font-weight: bold;
            margin-top: 20px;
        }
        ul{
            margin-bottom: 0;
            padding-bottom: 15px;
            border-bottom: 1px solid rgba(204, 204, 204, 0.541);
        }
    }
    .article-detail{
        padding: 30px 0;
        .btn-blog{
            font-size: 14px;
            svg{
                width: 20px;
                min-width: 20px;
            }
            &.left{
                svg{
                    margin-right: 4px;
                }
            }
        }
        .btn-bottom {
            .btn-blog{
                margin-top: 30px;
                font-size: 14px;
                svg{
                    width: 20px;
                    min-width: 20px;
                }
                &.right{
                    svg{
                        margin-left: 4px;
                    }
                }
                &.left{
                    svg{
                        margin-right: 4px;
                    }
                }
                img{
                    display: none;
                }
            }
        }
    }
    .banner-mid-coming{
        .content{
            padding: 0 15px;
            .sub-title{
                font-size: 14px;
            }
            .title{
                font-size: 24px;
                line-height: 30px;
            }
            .text{
                font-size: 14px;
            }
        }
        
    }
    .form-comingsoon{
        padding: 30px 15px;
        .sub-title{
            font-size: 14px;
        }
        .text{
            font-size: 16px;
            line-height: 24px;
        }
        button{
            img{
                display: none;
            }
        }
    }
    .coming-soon-banner {
        .content {
            .title{
                font-size: 30px;
            }
            .text{
                font-size: 24px;
            }
        }
    }
    .article-list{
        padding: 30px 0px;
        
    }
}

@media screen and (max-width: 464.98px){
    .home-block-5{
        margin-bottom: -117px;
    }
}