.coming-soon-banner{
    position: relative;
    &::before{
        content: '';
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background-color: #1a120a;
        opacity: 0.7;
        position: absolute;
    }
    .content{
        position: absolute;
        width: 100%;
        text-align: center;
        margin-top: 220px;
        color: var(--cwhite);
        .title{
            font-size: 2.92vw;
            margin-bottom: 20px;
            text-shadow: 2px 2px var(--cblack);
        }
        .text{
            font-size: 1.95vw;
            font-family: var(--font-title);
            text-shadow: 2px 2px var(--cblack);
        }
    }
}
  
.coming-soon-header{
    #res-menu-icon{
        display: none;
    }
    &.active{
        #res-menu-icon{
            display: block;
        }
    }
    svg{
        path{
            fill: var(--cwhite);
        }
    }
    a:hover{
        svg path{
            fill: var(--chover);
        }
    }
    .right {
        
        li:not(:last-child){
            margin-right: 13px;
        }
        li.th-lang{
            margin-right: 5px;
            &::after{
                content: '|';
                margin-left: 5px;
                color: var(--cwhite);
            }
        }
        .th-lang, .en-lang{
            text-transform: uppercase;
        }
    }
    #btn-subscribe-header{
        border: 1px solid var(--cwhite);
        border-radius: 5px;
        background-color: rgba(255, 255, 255, 0.3);
        padding: 5px;
        line-height: 100%;
        &:hover{
            background-color: rgba(255, 255, 255, 0.5);
            color: var(--cblack);
        }
    }
}

.form-comingsoon{
    position: relative;
    padding: 80px;
    color: var(--cmain);
    .img-bg-1{
        position: absolute;
        top: 0;
        left: 50px;
        z-index: -1;
    }
    .sub-title{
        font-size: 24px;
        margin-bottom: 10px;
    }
    .title{
        font-size: 42px;
        margin-bottom: 10px;
    }
    .text{
        font-size: 24px;
        line-height: 34px;
        margin-bottom: 50px;
    }
    .text-sub{
        font-size: 42px;
        margin-bottom: 20px;
        font-family: var(--font-title);
    }
    .wrap-input{
        @include flexbox();
        display: inline-flex;
        border: 1px solid var(--cmain);
        border-radius: 5px;
        overflow: hidden;
    }
    input{
        padding: 6px 14px;
        font-size: 20px;
        border: none;
        padding-top: 10px;
    }
    button{
        background-color: var(--chover);
        color: var(--cwhite);
        border: none;
        padding: 0 20px;
        font-size: 20px;
        line-height: 44px;
        img{
            margin-left: 10px;
            padding-bottom: 4px;
            transition: all 0.2s;
        }
        &:hover{
            img{
                transform: translateX(5px);
            }
        }
    }
}

.banner-mid-coming{
    text-align: center;
    height: 300px;
    min-height: 300px;
    position: relative;
    &::before{
        content: '';
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background-color: #162917;
        opacity: 0.7;
        position: absolute;
    }
    .content{
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        display: inline-block;
        color: var(--cwhite);
        width: 100%;
        .title{
            margin: 13px 0;
            text-shadow: 2px 2px var(--cblack);
        }
        .sub-title{
            font-size: 24px;
            font-weight: normal;
            letter-spacing: 0.5px;
        }
        .text{
            font-size: 18px;
        }
    }
}